  .c3-dropdown-panel {
    background: white;
    color: rgba(0, 0, 0, 0.87);
    border-radius: 4px;
    min-width: 112px;
    overflow: auto;
    max-height: calc(100vh - 48px);
    border-radius: 4px;
    outline: 0;
    padding: 4px;
    transition: box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1);
    box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14), 0px 3px 14px 2px rgba(0, 0, 0, 0.12);
  }
  .c3-file-container {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }
  .c3-file-container .btn-container {
    position: absolute;
    z-index: 98;
    text-align: right;
    bottom: 0;
    right: 0;
    width: 100%;
  }
  .c3-file-container img {
    max-width: 100%;
    max-height: 100%;
  }
  .c3-file-container a,
  .c3-file-container button {
    z-index: 99;
    position: relative;
  }
  .c3-file-container a:not(:disabled),
  .c3-file-container button:not(:disabled) {
    cursor: pointer;
  }
  button.default,
  a.default {
    height: 40px;
    width: 40px;
    opacity: 0.7;
    transition: opacity 200ms;
  }
  button.default:hover,
  a.default:hover {
    opacity: 1;
  }
  button.default:disabled,
  a.default:disabled {
    opacity: 0.25;
  }
  .nav-button-container > button,
  .nav-button-container > a {
    position: relative;
    right: 0;
    margin: 0 10px;
  }
  .nav-button-container {
    text-align: center;
    position: absolute;
    z-index: 98;
    bottom: 50px;
    left: 0;
    right: 0;
  }
  .spinner-container {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 60px;
    height: 60px;
    margin: auto;
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 25%;
  }
  .spinner-container .spinner {
    border-width: 7px;
    border-style: solid;
    border-color: #ccc;
    border-bottom-color: #222;
    border-radius: 50%;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    animation: rotation 2s linear infinite;
  }
  @keyframes rotation {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(359deg);
    }
  }
  .c3-file-viewer-pdf {
    height: 100%;
    width: 100%;
    position: relative;
  }
  .c3-file-viewer-pdf .download-button {
    position: absolute;
    bottom: 10px;
    right: 10px;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    color: #fff;
    font-size: 24px;
    cursor: pointer;
    padding: 10px;
  }
  .c3-file-viewer {
    display: block;
    height: 100%;
    width: 100%;
  }
  .c3-expansion {
    display: block;
    width: 100%;
  }
  .c3-expansion .mat-toolbar {
    padding: 0 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(63, 63, 70, var(--tw-bg-opacity));
  }
  .c3-expansion .c3-expansion-header {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
  }
  .c3-expansion .c3-expansion-header h4 {
    font-size: 1.125rem;
    line-height: 1.75rem;
  }
  .c3-flowing-menu {
    --menu-inactive: #dfdfdf;
    --menu-active: #c0c0c0;
    --menu-hover: #e7e7e7;
    --menu-border-color: #ddd;
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-areas: "menu contenu";
    height: 100%;
  }
  .c3-flowing-menu .menu {
    grid-area: menu;
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    padding: 2rem 0;
  }
  .c3-flowing-menu .menu > button {
    position: relative;
    margin: 0;
    padding: 1rem;
    background-color: var(--menu-inactive);
    cursor: pointer;
    min-width: 200px;
    box-shadow: none;
    border-radius: 0;
    border-top-left-radius: 1rem;
    border-bottom-left-radius: 1rem;
    border-bottom: 1px solid var(--menu-border-color);
    color: black;
  }
  .c3-flowing-menu .menu > button:hover {
    background-color: var(--menu-hover);
  }
  .c3-flowing-menu .menu > button.active {
    background-color: var(--menu-active);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    z-index: 1;
  }
  .c3-flowing-menu .menu > button.active::before {
    content: "";
    position: absolute;
    background-color: transparent;
    bottom: -2rem;
    right: 0;
    height: 2rem;
    width: 1rem;
    border-top-right-radius: 1rem;
    box-shadow: 0 -1rem 0 0 var(--menu-active);
  }
  .c3-flowing-menu .menu > button.active::after {
    content: "";
    position: absolute;
    background-color: transparent;
    top: -2rem;
    right: 0;
    height: 2rem;
    width: 1rem;
    border-bottom-right-radius: 1rem;
    box-shadow: 0 1rem 0 0 var(--menu-active);
  }
  .c3-flowing-menu .contenu {
    grid-area: contenu;
    padding: 1rem;
    height: 100%;
    overflow: auto;
    background-color: var(--menu-active);
    border-radius: 1rem;
    box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14), 0px 3px 14px 2px rgba(0, 0, 0, 0.12);
    transition: all 0.2s;
  }
  c3-menu #app-menu {
    position: absolute;
    top: 0;
    min-width: 290px;
    height: 100%;
    left: -240px;
    z-index: 999;
    box-sizing: border-box;
    transition: left 0.3s;
    background-color: var(--background-background);
    box-shadow: 3px 0 6px rgba(0, 0, 0, 0.3);
  }
  c3-menu #app-menu:hover {
    left: 0;
  }
  c3-menu #app-menu .ui-scroll {
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    scrollbar-width: thin;
    scrollbar-color: rgba(0, 0, 0, 0.2) transparent;
  }
  c3-menu #app-menu .ui-scroll ul {
    padding: 10px 0;
    list-style-type: none;
    margin: 0;
    width: 100%;
    box-sizing: border-box;
  }
  c3-menu #app-menu ul li {
    padding: 4px 5px;
    width: 100%;
    box-sizing: border-box;
  }
  c3-menu #app-menu ul li > a {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 10px;
    border-radius: 6px;
    color: inherit;
    text-decoration: none;
    box-sizing: border-box;
    transition: background-color 0.3s ease-in-out, transform 0.2s;
    overflow: hidden;
    position: relative;
    z-index: 0;
    line-height: 20px;
  }
}
@media (prefers-color-scheme: light) and (prefers-color-scheme: dark) {
  c3-menu #app-menu ul li > a:hover, c3-menu #app-menu ul li > a:focus {
    background-color: rgba(255, 255, 255, 0.2);
  }
}
@media (prefers-color-scheme: light) and (prefers-color-scheme: light) {
  c3-menu #app-menu ul li > a:hover, c3-menu #app-menu ul li > a:focus {
    background-color: rgba(0, 0, 0, 0.1);
  }
}
@media (prefers-color-scheme: light) {
  c3-menu #app-menu ul li > a .icon {
    width: 24px;
    height: 20px;
    font-size: 20px;
    position: absolute;
    right: 5px;
    top: 50%;
  }
  c3-menu #app-menu ul li > a span {
    display: inline-block;
    max-width: 220px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  c3-menu #app-menu ul li.active-item > a {
    background-color: var(--accent-color);
    font-weight: bold;
    color: inherit;
  }
  c3-menu .layout-main {
    margin-left: 60px;
    box-sizing: border-box;
    transition: margin-left 0.3s ease-in-out;
  }
  c3-menu .layout-main.headless {
    margin-left: 0;
  }
