  .spinner-container {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 60px;
    height: 60px;
    margin: auto;
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 25%;
  }
  .spinner-container .spinner {
    border-width: 7px;
    border-style: solid;
    border-color: #ccc;
    border-bottom-color: #222;
    border-radius: 50%;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    animation: rotation 2s linear infinite;
  }
  @keyframes rotation {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(359deg);
    }
