  .c3-flowing-menu {
    --menu-inactive: #9b9b9b;
    --menu-active: #5f5f5f;
    --menu-hover: #a5a5a5;
    --menu-border-color: #333;
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-areas: "menu contenu";
    height: 100%;
  }
  .c3-flowing-menu .menu {
    grid-area: menu;
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    padding: 2rem 0;
  }
  .c3-flowing-menu .menu > button {
    position: relative;
    margin: 0;
    padding: 1rem;
    background-color: var(--menu-inactive);
    cursor: pointer;
    min-width: 200px;
    box-shadow: none;
    border-radius: 0;
    border-top-left-radius: 1rem;
    border-bottom-left-radius: 1rem;
    border-bottom: 1px solid var(--menu-border-color);
    color: black;
  }
  .c3-flowing-menu .menu > button:hover {
    background-color: var(--menu-hover);
  }
  .c3-flowing-menu .menu > button.active {
    background-color: var(--menu-active);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    z-index: 1;
  }
  .c3-flowing-menu .menu > button.active::before {
    content: "";
    position: absolute;
    background-color: transparent;
    bottom: -2rem;
    right: 0;
    height: 2rem;
    width: 1rem;
    border-top-right-radius: 1rem;
    box-shadow: 0 -1rem 0 0 var(--menu-active);
  }
  .c3-flowing-menu .menu > button.active::after {
    content: "";
    position: absolute;
    background-color: transparent;
    top: -2rem;
    right: 0;
    height: 2rem;
    width: 1rem;
    border-bottom-right-radius: 1rem;
    box-shadow: 0 1rem 0 0 var(--menu-active);
  }
  .c3-flowing-menu .contenu {
    grid-area: contenu;
    padding: 1rem;
    height: 100%;
    overflow: auto;
    background-color: var(--menu-active);
    border-radius: 1rem;
    box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14), 0px 3px 14px 2px rgba(0, 0, 0, 0.12);
    transition: all 0.2s;
  }
  c3-menu #app-menu {
    position: absolute;
    top: 0;
    min-width: 290px;
    height: 100%;
    left: -240px;
    z-index: 999;
    box-sizing: border-box;
    transition: left 0.3s;
    background-color: var(--background-background);
    box-shadow: 3px 0 6px rgba(0, 0, 0, 0.3);
  }
  c3-menu #app-menu:hover {
    left: 0;
  }
  c3-menu #app-menu .ui-scroll {
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    scrollbar-width: thin;
    scrollbar-color: rgba(0, 0, 0, 0.2) transparent;
  }
  c3-menu #app-menu .ui-scroll ul {
    padding: 10px 0;
    list-style-type: none;
    margin: 0;
    width: 100%;
    box-sizing: border-box;
  }
  c3-menu #app-menu ul li {
    padding: 4px 5px;
    width: 100%;
    box-sizing: border-box;
  }
  c3-menu #app-menu ul li > a {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 10px;
    border-radius: 6px;
    color: inherit;
    text-decoration: none;
    box-sizing: border-box;
    transition: background-color 0.3s ease-in-out, transform 0.2s;
    overflow: hidden;
    position: relative;
    z-index: 0;
    line-height: 20px;
  }
}
@media (prefers-color-scheme: dark) and (prefers-color-scheme: dark) {
