  * {
    scrollbar-color: rgb(89.7945205479, 5.2054794521, 5.2054794521) hsla(0, 0%, 100%, 0);
  }
  html ::-webkit-scrollbar {
    background-color: transparent;
    width: 6px;
  }
  html ::-webkit-scrollbar-button:end:increment,
  html ::-webkit-scrollbar-button:start:decrement {
    background: transparent;
    display: none;
  }
  html ::-webkit-scrollbar-thumb:horizontal,
  html ::-webkit-scrollbar-thumb:vertical {
    display: block;
    background-color: rgb(89.7945205479, 5.2054794521, 5.2054794521);
    background-clip: content-box;
  }
  html ::-webkit-scrollbar-thumb:hover {
    display: block;
    background-clip: content-box;
    background-color: #8a0808;
  }
  ::-webkit-scrollbar {
    width: 3px;
  }
  ::-webkit-scrollbar-button:end:increment,
  ::-webkit-scrollbar-button:start:decrement {
    background: transparent;
    display: none;
  }
  ::-webkit-scrollbar-thumb:horizontal,
  ::-webkit-scrollbar-thumb:vertical {
    border-radius: 3px;
    display: block;
  }
  body {
    background: var(--surface-ground);
  }
}
body,
app-root,
c3-root {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

pre.hljs {
  padding: 1rem;
  border-radius: var(--border-radius-md);
  background: var(--code-bg) !important;
  margin: 0;
}

example-viewer {
  display: block;
  margin-bottom: 2rem;
}

html {
  scroll-behavior: smooth;
}

::selection {
  background: var(--c3-primary);
  color: white;
}

:focus-visible {
  outline: 2px solid var(--c3-accent);
  outline-offset: 2px;
}

.mat-mdc-card {
  background: var(--surface-card) !important;
  border: 1px solid var(--border-color);
}

.mat-mdc-tab-group .mat-mdc-tab-header {
  border-bottom: 1px solid var(--border-color);
}

.mat-mdc-button,
.mat-mdc-raised-button,
.mat-mdc-outlined-button {
  font-family: var(--font-body) !important;
  font-weight: 500 !important;
}

.mat-icon {
  vertical-align: middle;
}

* {
  scrollbar-width: thin;
  scrollbar-color: var(--border-color-hover) transparent;
}

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: var(--border-color-hover);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--text-muted);
}
